#root {
    margin: 0;
    padding: 0;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

input[type='number'] {
    -moz-appearance: textfield;
}

.c-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100vh;
}

.button-container {
    display: flex;
    width: 100%;
    justify-content: center;
    margin-bottom: 1rem;
    gap: 1rem;
    /* margin-top: 1rem; */
}

.img-container {
    position: relative;
    height: 40%;
}

.img-container img {
    width: 100%;
    height: 100%;
}

.inputs-container {
    display: flex;
    gap: 1rem;
    margin-top: 1rem;
    margin-bottom: 1rem;
}

.total-container {
    position: absolute;
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-rows: 1fr 1fr 1fr;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 1.5rem;
    padding: 1rem;
}

.cross-weights {
    flex: 1;
    display: grid;
    grid-template-rows: 1fr;
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
    margin-top: 0.5rem;
    margin-bottom: 0.5rem;
}
